import React from "react"
import styles from "./RSVP.module.scss"


import { TextBlock } from "components/Text Block"

const GOOGLE_FORM_URL_PREFIX = "https://docs.google.com/forms/d/e/1FAIpQLSfhOY-0puP9d9enzJLAEVVjKc-bPmqUG7c91JgSjxvMiS0ytA/viewform?usp=pp_url&entry.34784408=";


const extractFirstNamesAsString = (names) => {
    console.log(names);
    return names.map((name) => name.split(' ')[0]).join(", ");
}

const RSVP = ({ loginDetails }) => {

    const numberOfGuests = loginDetails.invitation.NUMBER_OF_GUESTS;
    const names = extractFirstNamesAsString(loginDetails.invitation.NAMES);

    const message= `${numberOfGuests} of us: ${names}`;

    const formURL = GOOGLE_FORM_URL_PREFIX + message;

    return (
        <>
            <iframe src={formURL} className={styles.container} title="rsvpForm">Loading…</iframe>
        </>
    )
}

export default RSVP
