import React from "react"

import { Login } from "components/Login"

const ProtectedComponent = ({ loginDetails, updateLoginDetailsHandler, Component }) => {

    if (!loginDetails) {
        return (
            <>
                <Login updateLoginDetailsHandler={updateLoginDetailsHandler} />
            </>
        )
    }

    return (
        <>
            <Component loginDetails={loginDetails} />
        </>
    )
}

export default ProtectedComponent
