import { Alert, Spinner } from "react-bootstrap";
import React, { useState } from "react"
import { Form, Button } from "react-bootstrap"

import styles from "./Login.module.scss"
import fetchInvitation from "services/invitation-service.js"

const Login = ({ updateLoginDetailsHandler }) => {
    const [loggedInGuestName, setLoggedInGuestName] = useState('');
    const [nameInput, setNameInput] = useState(null);
    const [passcodeInput, setPasscodeInput] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [isInvalidInput, setIsInvalidInput] = useState(false);

    const onLoginSuccess = (invitation) => {
        setIsLoading(false);
        setLoggedInGuestName(nameInput);
        updateLoginDetailsHandler({ currentGuestName: nameInput, invitation });
    }

    const onLoginFailure = (invitation) => {
        setIsInvalidInput(true);
        setIsLoading(false);
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        setIsLoading(true);
        setIsInvalidInput(false);
        setLoggedInGuestName('');
        fetchInvitation(nameInput, passcodeInput, onLoginSuccess, onLoginFailure);
    }

    return (
        <>
            <Form className={styles.sign_in + " pull-left"} onSubmit={(e) => handleSubmit(e)}>
                <Form.Group>
                    <Form.Label>Please enter your name</Form.Label>
                    <Form.Control
                        type="text"
                        onChange={(e) => setNameInput(e.target.value)}
                    ></Form.Control>
                </Form.Group>
                <Form.Group>
                    <Form.Label>Please enter your passcode</Form.Label>
                    <Form.Control
                        type="text"
                        onChange={(e) => setPasscodeInput(e.target.value)}
                    ></Form.Control>
                </Form.Group>
                <Button
                    variant="light"
                    type="submit"
                    disabled={isLoading}
                >
                    Submit
                </Button>
                {isLoading &&
                    <Spinner
                        animation="border"
                        size="sm"
                        className={styles.spinner}
                    />
                }
                <br></br><br></br>
                {
                    isInvalidInput &&
                    <Alert variant="danger">
                        Oh, no! It seems we cannot find your invitation.
                        Maybe check for typos? If you feel this is an error, please&nbsp;
                        <a href="mailto:hello@colinandtanay.com">
                            email us
                        </a>.
                    </Alert>
                }
                {
                    loggedInGuestName &&
                    <Alert variant="info">
                        Hello {loggedInGuestName.substr(0, loggedInGuestName.indexOf(' '))} !
                    </Alert>
                }
            </Form>
        </>
    )
}

export default Login