import React from "react"
import Container from "react-bootstrap/Container"

import { schedule } from "content/Schedule"
import { TextBlock } from "components/Text Block"
import { Item } from "components/Item"

import image from "photos/conrad_pune.png"
import styles from "./Schedule.module.scss"


const Schedule = ({ loginDetails }) => {

    const scheduleItems = schedule

    const isInvitedToEvent = (event) => {
        return loginDetails.invitation[event] === "TRUE";
    }

    return (
        <>

            <TextBlock
                text="Event Schedule!">
            </TextBlock>
            <div className={styles.image_div}>
                <a href="https://g.page/ConradPune?share" target="_blank" rel="noopener noreferrer">
                    <img src={image} className={styles.image} alt="Conrad"></img>
                </a>
                <br/>
                <p className={styles.image_text}>CONRAD,  PUNE</p>
            </div>
            <Container fluid>
                {scheduleItems.map(
                    (
                        {
                            id,
                            Title,
                            Date,
                            Location,
                            Summary,
                            Details,
                            
                        },
                        idx
                    ) => (isInvitedToEvent(id) &&
                        <Item
                            key={idx}
                            LeftTitle={Title}
                            LeftFirstSubtitle={Date}
                            LeftSecondSubtitle={Location}
                            RightBody={Summary}
                            HiddenFirstBody={Details}
                        />
                       
                    )
                )}
            </Container>
        </>
    )
}

export default Schedule
