import React, { useState } from "react"
import { NavLink, withRouter } from "react-router-dom"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"

import { navigation } from "content"

import styles from "./Navigation.module.scss"

function Navigation({ loginDetails }) {
    const {
        schedule: ScheduleNavText,
        travel: TravelNavText,
        thingsToDo: ThingsToDoNavText,
        faq: FAQNavText,
        rsvp: RSVPNavText,
    } = navigation

    const [expanded, setExpanded] = useState(false);

    return (
        <Navbar
            variant="light"
            expand="sm"
            sticky="top"
            className={styles.navigation}
            expanded={expanded}
        >
            <Navbar.Toggle onClick={() => setExpanded(expanded ? false : "expanded")} />
            <Navbar.Collapse>
                <Nav>
                    <NavLink
                        to="/"
                        className="nav-link"
                        role="button"
                        onClick={() => setExpanded(false)}
                    >
                        Home
                    </NavLink>
                    <NavLink
                        to="/schedule"
                        className="nav-link"
                        role="button"
                        onClick={() => setExpanded(false)}
                    >
                        <ScheduleNavText />
                    </NavLink>
                    <NavLink
                        to="/travel"
                        className="nav-link"
                        role="button"
                        onClick={() => setExpanded(false)}
                    >
                        <TravelNavText />
                    </NavLink>
                    <NavLink
                        to="/things-to-do"
                        className="nav-link"
                        role="button"
                        onClick={() => setExpanded(false)}
                    >
                        <ThingsToDoNavText />
                    </NavLink>
                    <NavLink
                        to="/faq"
                        className="nav-link"
                        role="button"
                        onClick={() => setExpanded(false)}
                    >
                        <FAQNavText />
                    </NavLink>
                    <NavLink
                        to="/rsvp"
                        className="nav-link"
                        role="button"
                        onClick={() => setExpanded(false)}
                    >
                        <RSVPNavText />
                    </NavLink>
                </Nav>
            </Navbar.Collapse>
            <Nav>
                    <NavLink
                        to="/login"
                        className="nav-link "
                        role="button"
                        onClick={() => setExpanded(false)}>
                        {loginDetails ? loginDetails.currentGuestName : "Login"}
                    </NavLink>
                </Nav>
        </Navbar>
    )
}

export default withRouter(Navigation)
