import React from "react"
import Container from "react-bootstrap/Container"

import { thingsToDo } from "content/ThingsToDo"
import { TextBlock } from "components/Text Block"
import { Item } from "components/Item"

function ThingsToDo() {

    const thingsToDoItems = thingsToDo

    return (
        <>
            <TextBlock
                text="Things to do in Pune!">
            </TextBlock>
            <br></br>
            <Container fluid>
                {thingsToDoItems.map(({ Title, Details }, idx) => (
                    <Item
                        key={idx}
                        LeftTitle={Title}
                        RightBody={Details}
                    />
                ))}
            </Container>
        </>
    )
}

export default ThingsToDo
