function fetchInvitation(name, password, success, fail) {

    var requestOptions = {
        method: 'GET',
    };

    var url = new URL("https://sd9qfk1d5e.execute-api.us-west-2.amazonaws.com/invite")
    var params = {guestName:name, passcode:password}
    Object.keys(params).forEach(key => url.searchParams.append(key, params[key]))

    fetch(url, requestOptions)
        .then(response => {
            if (response.ok) {
                return response.json()
            } else {
                throw Error(response)
            }
        })
        .then(response => success(response))
        .catch(error => fail(error));
}

export default fetchInvitation;