import React from "react"
import classNames from "classnames"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

import styles from "./Item.module.scss"
import { Accordion } from "react-bootstrap"
import { useAccordionToggle } from 'react-bootstrap/AccordionToggle';


const Item2 = ({
    LeftTitle = null,
    LeftFirstSubtitle = null,
    LeftSecondSubtitle = null,
    LeftThirdSubtitle = null,
    RightTitle = null,
    RightFirstSubtitle = null,
    RightSecondSubtitle = null,
    RightThirdSubtitle = null,
    RightBody = null,
    HiddenFirstBody = null,
    HiddenSecondBody = null,
    HiddenThirdBody = null,
}) => {

    let isCollapsed = true;
    const CustomToggle = ({ eventKey }) => {
        const decoratedOnClick = useAccordionToggle(eventKey, () => isCollapsed = !isCollapsed);
        return (
            <p className={styles.hiddenToggle} onClick={decoratedOnClick}>
                {isCollapsed ? 'More Details' : 'Less Details'}
            </p>
        );
    }

    const hasHiddenContent = (HiddenFirstBody
        || HiddenSecondBody
        || HiddenThirdBody
    );

    return (
        <Accordion>
            <Row className={styles.item}>
                <Col
                    sm={12}
                    md={{ span: 3, offset: 2 }}
                    className={classNames(styles.col, styles.left, "my-auto")}
                >
                    {LeftTitle && (
                        <h1 className={styles.title}>
                            <LeftTitle />
                        </h1>
                    )}
                    {LeftFirstSubtitle && (
                        <h4 className={classNames(styles.subtitle, "text-muted")}>
                            <LeftFirstSubtitle />
                        </h4>
                    )}
                    {LeftSecondSubtitle && (
                        <h4 className={classNames(styles.subtitle, "text-muted")}>
                            <LeftSecondSubtitle />
                        </h4>
                    )}
                    {LeftThirdSubtitle && (
                        <h4 className={classNames(styles.subtitle, "text-muted")}>
                            <LeftThirdSubtitle />
                        </h4>
                    )}
                </Col>
                <Col
                    sm={12}
                    md={5}
                    className={classNames(styles.col, styles.right, "my-auto")}
                >
                    {RightTitle && (
                        <h1 className={styles.title}>
                            <RightTitle />
                        </h1>
                    )}
                    {RightFirstSubtitle && (
                        <h4 className={classNames(styles.subtitle, "text-muted")}>
                            <RightFirstSubtitle />
                        </h4>
                    )}
                    {RightSecondSubtitle && (
                        <h4 className={classNames(styles.subtitle, "text-muted")}>
                            <RightSecondSubtitle />
                        </h4>
                    )}
                    {RightThirdSubtitle && (
                        <h4 className={classNames(styles.subtitle, "text-muted")}>
                            <RightThirdSubtitle />
                        </h4>
                    )}
                    {RightBody && (
                        <h4 className={styles.body}>
                            <RightBody />
                        </h4>
                    )}
                    {hasHiddenContent && 
                        <>
                            {/* <br></br> */}
                            <CustomToggle eventKey={"0"} />
                        </>
                    }
                    <Accordion.Collapse eventKey="0" className={styles.hidden}>
                        <>
                            {HiddenFirstBody && (
                                <h4 className={styles.body}>
                                    <HiddenFirstBody />
                                </h4>
                            )}
                            {HiddenSecondBody && (
                                <h4 className={styles.body}>
                                    <HiddenSecondBody />
                                </h4>
                            )}
                            {HiddenThirdBody && (
                                <h4 className={styles.body}>
                                    <HiddenThirdBody />
                                </h4>
                            )}
                        </>
                    </Accordion.Collapse>
                    

                </Col>
            </Row>

        </Accordion>
    )
}

export default Item2
