import React from "react"
import { Route, Switch } from "react-router-dom"

import { Home } from "views/Home"
import { Schedule } from "views/Schedule"
import { Travel } from "views/Travel"
import { ThingsToDo } from "views/ThingsToDo"
import { FAQ } from "views/FAQ"
import { RSVP } from "views/RSVP"
import { Login } from "components/Login"
import { ProtectedComponent } from "components/ProtectedComponent"


const AppLayout = ({ loginDetails, updateLoginDetailsHandler }) => {

    const createRoute = (path, component) => {
        return (
            <Route
                exact path={path}
                render={(props) => (
                    <ProtectedComponent {...props} updateLoginDetailsHandler={updateLoginDetailsHandler} loginDetails={loginDetails} Component={component}/>
                )}
            />
        )
    }

    return (
        <>
            <main className="mb-4">
                <Switch>
                    {createRoute("/", Home)};
                    {createRoute("/schedule", Schedule)};
                    {createRoute("/travel", Travel)};
                    {createRoute("/things-to-do", ThingsToDo)};
                    {createRoute("/faq", FAQ)};
                    {createRoute("/rsvp", RSVP)};
                    <Route
                        exact path={"/login"}
                        render={(props) => (
                            <Login {...props} updateLoginDetailsHandler={updateLoginDetailsHandler} loginDetails={loginDetails}/>)}
                    />
                </Switch>
            </main>
        </>
    );

}

export default AppLayout
