import React from "react"
import ReactDOM from "react-dom"
import { HashRouter, Route, Switch } from "react-router-dom"
import { useCookies } from 'react-cookie';

import "./index.css"
import "bootstrap/dist/css/bootstrap.min.css"

import { AppLayout } from "views/AppLayout"
import { Footer } from "components/Footer"
import { Header } from "components/Header"
import { Navigation } from "components/Navigation"
import { CookiesProvider } from 'react-cookie';

import styles from "./index.module.scss"

const App = () => {

    const [cookies, setCookie] = useCookies(['loginDetails']);
    const updateLoginDetailsHandler = (updatedLoginDetails) => {
        setCookie('loginDetails', updatedLoginDetails, { path: '/', maxAge: 180000 });
    }

    return (
        <>
            <div className={styles.background_wallpaper}>
                <HashRouter>
                    <Navigation loginDetails={cookies.loginDetails} />
                    <div className={styles.background_flower}>
                        <Header loginDetails={cookies.loginDetails} />
                        <br></br>
                        <Switch>
                            <Route render={(props) => (
                                <AppLayout {...props} updateLoginDetailsHandler={updateLoginDetailsHandler} loginDetails={cookies.loginDetails} />
                            )} />
                        </Switch>
                    </div>
                    <Footer></Footer>
                </HashRouter>
            </div>
        </>
    );

}

ReactDOM.render(
    <CookiesProvider>
        <App />
    </CookiesProvider>,
    document.getElementById("root")
)
